'use strict';
import {EventTarget} from "../common/event";
import {Point} from "../common/point";
import {PositionInfo} from "../common/positionInfo";
import {SynchronousJsonPortMarshaler} from "./marshaler";
import {IContextMenuProvider} from "../interfaces/iContextMenuProvider";
import {Promise} from "q";

export class ContextMenuMarshaler extends SynchronousJsonPortMarshaler {
    constructor(control: EventTarget, public contextMenuProvider: IContextMenuProvider) {
        super(control);
    }

    public callback(id: string): Promise<any> {
        return this.contextMenuProvider.callback(id);
    }

    public dismissAll(): Promise<any> {
        return this.contextMenuProvider.dismissAll();
    }

    public dismissCurrent(ignoreDismissForRoot: boolean): Promise<any> {
        return this.contextMenuProvider.dismissCurrent(ignoreDismissForRoot);
    }

    public dismissSubmenus(currentCoordinates: Point): Promise<any> {
        return this.contextMenuProvider.dismissSubmenus(currentCoordinates);
    }

    public contentready(): Promise<any> {
        return this.contextMenuProvider.contentready();
    }

    public show(id: string, ariaLabel: string, contextMenuHtml: string, positionInfo: PositionInfo): Promise<any> {
        return this.contextMenuProvider.show(id, ariaLabel, contextMenuHtml, positionInfo);
    }

    public contextMenuDismissed(): void {
        this.fireEvent('contextmenudismissed', {});
    }
}